@import url('./main.css');

.header {
    width: 80%;
    margin: auto;
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 5px;
    grid-auto-rows: minmax(50px, auto);
}

.nav {
    grid-column: 4/13;
    grid-row: 1;
    align-self: center;
}

.logo {
    grid-column: 1/4;
    grid-row: 1;
    align-items: center;
    justify-items: center;
    max-width: 300%;
    max-height: 300%;
}

.sticker {
    position: fixed;
    width: 10%;
    right: 100px;
    bottom: 50px;
}

@media (max-width: 768px) {
    body {
        color: red;
    }
}