@import url('./main.css');

li {
    color: white;
    display: inline-block;
    width: 20%;
    padding: 10px 0px;
    text-align: center;
}

li:hover {
    text-decoration: underline;
}

ul {
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 5px;
    background-color: var(--primary-color);

}

.nav {
    height: 33px;
}